<template>
    <div class="mobile-bind">
        <div class="cPBoxItm-title">
            <div></div>
            <span class="c-recovery" @click="shut">&#xe64e;</span>
        </div>
        <div class="changeSuccessful">
            <img src="./../../../../actual/publicImg/setSucc.png"/>
            <p>发送成功</p>
            <p>请登录邮箱确认绑定</p>
        </div>
    </div>
</template>
<script>
    export default{
        methods:{
            shut:function () {
                this.$emit('shut')
            }
        }
    }
</script>
<style scoped>
    .mobile-bind{
        left: 0px !important;
        top: 5px !important;
    }
</style>


