<template>
    <div class="mobile-bind">
        <div class="cPBoxItm-title">
            <div></div>
        </div>
        <div class="changeSuccessful">
            <img src="./../../../../actual/publicImg/setSucc.png"/>
            <p>修改成功</p>
            <p>{{time}}s后跳转到登录页面</p>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
          return{
              time:3
          }
        },
        mounted:function () {
            setInterval(()=>{
                this.time--;
                if(this.time==1){
                    location.href = "/outLogin";
                }
            },1000)
        },
        methods:{
            shut:function () {
                this.$emit('shut')
            }
        }
    }
</script>
<style>

</style>


