<template>
    <div class="personal-center-cPassword">
        <div class="personal-center-cPBth">
            <h5>修改密码：</h5>
        </div>
        <div class="personal-center-cPCont">
            <p>为了您的账号安全，请定期修改密码</p>
            <div class="cPBoxItm-pas nice-btn-dBlue" @click="open">修改密码</div>
        </div>
        <validation v-if="shos" v-on:swit="swit" v-on:thisKg="thisKg"></validation>
        <nextOperation v-if="nshos" v-on:thisKg="thisKg" v-on:scu="scu"></nextOperation>
        <success v-if="scues"></success>
    </div>
</template>
<script>
    import validation from '../public/validation.vue'
    import nextOperation from '../pwdChange/nextOperation.vue'
    import success from '../pwdChange/pwdsuccess.vue'
    export default{
        data(){
            return{
                shos:false,
                nshos:false,
                scues:false
            }
        },
        methods:{
            scu:function () {
                this.shos=false;
                this.nshos=false;
                this.scues=true;
            },
            open:function () {
                this.shos=true;
            },
            swit:function () {
                this.shos=false;
                this.nshos=true
            },
            thisKg:function () {
                this.shos=false;
                this.nshos=false;
            }
        },
        components:{
            validation:validation,
            nextOperation:nextOperation,
            success:success
        }
    }
</script>
<style scoped>
    .personal-center-cPassword {
        position:relative;
        height:380px;
    }
</style>


